
/* 让应用高度跟随宿主 iframe；滚动交给子容器 */
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

/* 过渡动画 */
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

/* 全局滚动条样式覆盖 */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

::-webkit-scrollbar-track {
  background: transparent;
}

::-webkit-scrollbar-thumb {
  background: rgba(100, 116, 139, 0.5);
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(71, 85, 105, 0.8);
}

// 保留原有的 CSS 变量定义，以兼容旧组件（如果需要）
:root {
  --bg-primary: #f8f9fa;
  --text-primary: #2c3e50;
  /* ... 其他变量保留 ... */
}

[data-theme='dark'] {
  --bg-primary: #020617; /* slate-950 */
  --text-primary: #ffffff;
  /* ... */
}

/* #app 作为容器占满 iframe */
#app {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}
